#jobs-component {

    display: flex;
    flex-direction: column;
    height: 100%;

    .jobs-list {
        overflow: auto;

        .one-job-card {
            background-color: rgba(21, 145, 240, 0.3);

            margin: var(--medium_size_margin_em);
            padding: var(--medium_size_padding_em);

            border-radius: var(--small_border_radius_px);

            .chips {
                margin: var(--medium_size_margin_em);
                background-color: rgba(210, 240, 255, 0.7);

                color: #333;
                border-radius: var(--small_border_radius_px);

                font-weight: bolder;

                user-select: "none";
                text-decoration: none;
            }
        }
    }
}
