#about-me-component {
  
  display: flex;
  flex-direction: column;

  flex-grow: 1;
  height: 100%;

  width: 100%;

  p {
    margin: var(--medium_size_margin_em);
  }

  .about-me-content {
    overflow: auto;

    div:nth-child(2) {
      margin-bottom: 0.8em;
      .chips {
        margin: var(--medium_size_margin_em);
        background-color: rgba(210, 240, 255, 0.7);
        color: #333;
        border-radius: var(--small_border_radius_px);
        font-weight: bolder;
        user-select: "none";
        text-decoration: none;
      }

      .contact-hover:hover {
        background-color: rgba(210, 240, 255, 1);
      }
    }
  }
}
