#content-component {
    display: flex;
    flex-direction: column;
    align-items: center;

    flex-grow: 1;
    height: 100%;

    h2 {
        height: min-content;
        width: 100%;
        text-align: center;
    }

    #contnet-with-cards {
        overflow: auto;
        display: flex;
        align-items: start;
        justify-content: center;
        align-content: flex-start;
        flex-direction: row;

        flex-wrap: wrap;
        width: 100%;

        .card-item-component {
            display: flex;
            flex-direction: column;

            background-color: rgba(215, 255, 255, 0.42);

            border-radius: var(--small_border_radius_px);

            margin: var(--medium_size_margin_em);
            padding: var(--medium_size_padding_em);

            @media screen and (min-width: 30cm) {
                margin: 2em;
                padding: 0.5em;
            }

            max-width: 20cm;
            width: 100%;

            .card-item-header {
                width: 100%;

                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
                flex-wrap: wrap;

                a {
                    text-decoration: none;
                    color: black;
                }

                div {
                    a {
                        button {
                            background-color: #1976d2;
                        }
                    }
                }
            }

            .card-item-main-content {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                align-items: center;

                .card-item-description-wrapper {
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;

                    width: 100%;

                    article {
                        display: flex;
                        align-items: center;
                    }

                    .chips {
                        display: flex;
                        justify-content: flex-start;
                        flex-wrap: wrap;

                        flex-grow: 1;
                        margin: var(--medium_size_margin_em);

                        .one-chip {
                            margin: var(--small_size_margin_em);
                            border-radius: var(--small_border_radius_px);
                            background-color: #1976d2;
                            font-weight: bolder;

                            color:white;

                            user-select: "none";
                            text-decoration: none;
                        }
                    }
                }

                img,
                iframe {
                    width: 100%;
                    height: 100%;

                    margin: 0;
                    padding: 0;
                    border: 0;

                    border-radius: var(--small_border_radius_px);
                }

                .project-cover-video-wrapper {
                    height: 30em;
                    width: 100%;
                }
            }
        }
    }
}
