#sidebar-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: start;
    height: 100%;

    flex-grow: 1;

    /* Handle (thumb) */
    ::-webkit-scrollbar-thumb {
        border-radius: var(--small_border_radius_px) !important;
    }

    #avatar-wrapper {
        width: 100%;
        max-height: fit-content;

        #profile-image {
            width: 100%;
            height: 100%;
            max-height: fit-content;
            border-radius: var(--smaller_border_radius_px);
        }
    }

    #under-avatar-wrapper {
        overflow: auto;
        height: 100%;

        display: flex;
        justify-content: space-between;
        flex-direction: column;

        margin-top: var(--small_size_margin_em);

        h1,
        h3 {
            text-align: center;
        }

        h1 {
            p {
                margin: 0;
                padding: 0;
            }
        }

        nav {
            display: flex;
            margin-top: var(--small_size_margin_em);

            height: min-content;
            flex-direction: column;

            flex-grow: 1;

            button {
                margin: var(--medium_size_margin_em);
                display: flex;
                justify-content: space-between;
                justify-items: center;
                align-items: center;
            }
        }
    }
}
