:root {
  --small_size_margin_em: 0.2em;
  --medium_size_margin_em: 0.4em;

  --small_size_padding_em: 0.2em;
  --medium_size_padding_em: 0.4em;

  --smaller_border_radius_px: 6px;
  --small_border_radius_px: 16px;
}

html,
body {
  height: 100%;
  width: 100%;

  margin: 0;
  padding: 0;

  user-select: none;
  overflow: hidden;

  background-color: #008cff4d;
  //background-image: linear-gradient(to bottom, #008cff 0%, #51bfff 100%);
}

body {
  font-family:
    -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

#root {
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;

  ::selection {
    //background-color: #a2ddff;
    //background-color: #1976d2;
    background-color: rgba(25, 118, 210, 0.3);
  }

  /* Target the scrollbar */
  ::-webkit-scrollbar {
    width: min-content;
  }

  /* Track (background) */
  ::-webkit-scrollbar-track {
    border-radius: var(--small_border_radius_px);
  }

  ::-webkit-scrollbar-corner {
    background-color: none;
  }

  /* Handle (thumb) */
  ::-webkit-scrollbar-thumb {
    background: #a2ddff;
    height: 0.5cm;

    &:vertical {
      border-radius: var(--small_border_radius_px) 0 0 var(--small_border_radius_px);
    }

    &:horizontal {
      border-radius: var(--small_border_radius_px);
    }
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover,
  ::-webkit-scrollbar-thumb:active {
    background: #1976d2;
  }

  header {
    max-width: min-content;
    margin: var(--medium_size_margin_em);
  }
  main {
    width: 100%;
    margin: var(--medium_size_margin_em);
    margin-right: 0;
    overflow: auto;
  }
}
