#projects-component {

    display: flex;
    flex-direction: column;

    flex-grow: 1;
    height: 100%;

    #projects-summary {
        overflow: auto;
        padding: 0 var(--medium_size_padding_em);
    }

    #component-wrapper {
        display: flex;
        flex-direction: column;
        overflow: auto;
    }

    #navigation-to-projects-wrapper {
        display: flex;
        flex-grow: 1;

        justify-content: center;
        align-items: flex-end;
        align-content: center;

        margin: var(--medium_size_margin_em);

        /* Handle (thumb) */
        ::-webkit-scrollbar-thumb {
            border-radius: var(--small_border_radius_px) !important;
        }

        #group-of-buttons {
            display: flex;
            flex-wrap: wrap;

            overflow-y: auto;
            overflow-x: hidden;
            max-height: 4cm;

            @media (max-width: 50cm) {
                width: 100%;
            }

            button {
                height: max-content;
                flex-grow: 1;
                margin: var(--medium_size_margin_em);
                align-self: flex-end;
            }
        }
    }

    /*  #projects-wrapper {
        flex-grow: 1;
        height: 100%;
        overflow: auto;

        background-color: red;
    }*/
}
